.main-header {
  min-height: 56px;
}
.main-header__nav {
  z-index: 2;
  width: 320px;
  position: relative;
}
.main-header__nav--opened {
  position: absolute;
  background-color: #81b3d2;
}
@media (min-width: 768px) {
  .main-header__nav {
    width: auto;
    background-color: transparent;
  }
}
.main-header__logo {
  z-index: 1;
  width: 185px;
  height: 96px;
  position: absolute;
  left: 0;
  right: 0;
  margin: 0 auto;
}
.main-header__logo-image {
  display: block;
}
.main-header__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
@media (min-width: 768px) {
  .main-header__list {
    padding-left: 40px;
    padding-right: 19px;
  }
}
@media (min-width: 1280px) {
  .main-header__list {
    padding-left: 82px;
    padding-right: 61px;
  }
}
.main-header__toggle {
  z-index: 3;
  position: absolute;
  display: block;
  width: 23px;
  height: 23px;
  right: 20px;
  top: 17px;
  border: none;
  background-color: transparent;
  cursor: pointer;
}
@media (min-width: 768px) {
  .main-header__toggle {
    display: none;
  }
}
.main-header__nav--closed .main-header__toggle::before {
  content: "";
  position: absolute;
  display: block;
  width: 23px;
  height: 4px;
  top: 0;
  right: 0;
  background-color: #81b3d2;
  box-shadow: 0 9px 0 0 #81b3d2, 0 18px 0 0 #81b3d2;
}
.main-header__nav--closed .main-header__toggle:hover::before {
  background-color: #81b3d260;
  box-shadow: 0 9px 0 0 #81b3d260, 0 18px 0 0 #81b3d260;
}
.main-header__nav--closed .main-header__toggle:active::before {
  background-color: #81b3d230;
  box-shadow: 0 9px 0 0 #81b3d230, 0 18px 0 0 #81b3d230;
}
@media (max-width: 767px) {
  .main-header__nav--closed .site-list__item {
    display: none;
  }
  .main-header__nav--nojs .main-header__toggle {
    display: none;
  }
  .main-header__nav--nojs .site-list__item {
    display: block;
    background-color: #81b3d2;
  }
  .main-header__nav--nojs .site-list {
    position: static;
    background-color: #81b3d2;
  }
}
.main-header__nav--opened .main-header__toggle::before,
.main-header__nav--opened .main-header__toggle::after {
  content: "";
  position: absolute;
  width: 28px;
  height: 4px;
  top: 9px;
  right: -2px;
  background-color: #ffffff;
}
.main-header__nav--opened .main-header__toggle::before {
  transform: rotate(45deg);
}
.main-header__nav--opened .main-header__toggle::after {
  transform: rotate(-45deg);
}
.main-header__nav--opened .main-header__toggle:hover::before,
.main-header__nav--opened .main-header__toggle:hover::after {
  background-color: rgba(255, 255, 255, 0.6);
}
.main-header__nav--opened .main-header__toggle:active::before,
.main-header__nav--opened .main-header__toggle:active::after {
  background-color: rgba(255, 255, 255, 0.3);
}
